.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  text-align: center;
  background-color: #012265;
  overflow: y - scroll;
  padding: 5px;
}

.inputs-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.input-container {
  margin: 3px;
  padding: 3px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #d4af37;
  width: 30%;
  text-align: center;
  box-sizing: border-box;
}

.input-box {
  padding: 3px;
  margin-bottom: 3px;
  font-size: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 90%;
  text-align: center;
  background-color: white;
  color: white;
}

.input-box::placeholder {
  color: white;
  opacity: 0.7;
}

.top-input {
  background-color: #0A3D91;
}

.bottom-input {
  background-color: #B22222;
}

.team-data-container {
  text-align: center;
  background-color: #d4af37;
  border-radius: 5px;
  padding: 3px;
  margin-top: 0;
}

.section-header {
  font-weight: bold;
  margin-top: 3px;
  margin-bottom: 3px;
  font-size: 12px;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1px;
}

.grid-item {
  border: 1px solid #ddd;
  padding: 1px;
  background-color: #f2f2f2;
  font-size: 12px;
}

.heatmap-container {
  width: 100%;
  max-width: 800px;
  height: 450px;
  position: relative;
  margin-top: 10px;
  background-color: black; /* To visualize the container */
}
